:root {
    --primary: #d81e5b;
    --secondary: #8c38ff;
    --dark: #131a26;
    --light: #eee;
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Fira Sans', sans-serif;
  }
  
  body {
    background-color: var(--light) ;
  }
    
  .App {
    display: flex;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
   }
  
  .Calculator {
    display: flex;
    min-height: 100vh;
    width: 500px;
    align-items: center;
    justify-content: center;
   }
  
  .calculator {
    padding-top: 50px;
    padding-right: 3px;
    width: 100%;
    max-width: 500px;
    height: 600px;
    background-color:pink;
    border-radius: 7%;
    overflow: hidden;
    box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
    align-items: center;
    border: 10px solid black;
    box-shadow: 20px 20px grey;
  }
  
  .display {
    padding: 16px;
    text-align: right;
    background-color: rgb(189, 185, 185);
    color: var(--dark);
    font-size: xx-large;
    font-weight: bolder;
    border-radius: 10px;
    margin-left: 3px;
    box-shadow: 3px 3px grey;
    margin-bottom: 5px;
  }
  
  .display span {
    font-size: 14px;
    color: rgb(0, 0, 0);
  }
  
  button {
    appearance: none;
    border: none;
    outline: none;
    color: rgb(0, 0, 0);
    font-size: 20px;
    padding: 16px;
    cursor: pointer;
    border-radius: 10px;
  }
  
  button:hover {
    opacity: 0.9
  }
  
  .operators {
    display: flex
  }
  
  .operators button {
    flex: 1 1 0%;
    max-width: 31.9999%;
    background-color: rgba(0, 174, 255, 0.959);;
    font-weight: 500;
    margin-left: 3px;
    margin-bottom: 2px;
    margin-top: 2px;
    font-size: 25px;
  }
  .clear{
    display: inline-flex;
    width: 100%;
  }
  
  .digits {
    display: flex;
    flex-wrap: wrap;
  }
  
  .digits button {
    flex:  1 1 33.333%;
    max-width: 31.9999%;
    background-color: var(--dark);
    border: 1px solid black;
    font-weight: 500;
    margin-left: 3px;
    margin-bottom: 2px;
    font-size: 25px;
    color: #eee;
  }

.delete{
  appearance: none;
  border: none;
  outline: none;
  color: rgb(0, 0, 0);
  font-size: 25px;
  padding: 16px;
  cursor: pointer;
  border-radius: 10px;
  background-color: hsl(145, 100%, 36%);
